<template>
  <div class="code row">
    <div class="secret socket">?</div>
    <div class="secret socket">?</div>
    <div class="secret socket">?</div>
    <div class="secret socket">?</div>
  </div>
</template>

<script>
import mastermind from '../repositories/mastermind'
import pins from '../utils/pins'

export default {
  name: 'HiddenRow',

  props: [
    'id'
  ],

  data() {
    return {
      pins: []
    }
  },

  computed: {
    allPins () {
      var pins = this.pins.slice();

      while(pins.length < 4) {
        pins.push(null)
      }

      return pins;
    }
  },

  methods: {
    checkStatus() {
      mastermind.getStatus(this.id)
        .then(data => {
          if(data.isWon === true) {
            window.EventBus.$emit('status:won', data.rowsPlayed)

            this.$swal({
              icon: 'success',
              title: 'You won!',
              text: 'I doubt if it\'s the Mastermind skills or coding skills...'
            })
          }

          if(data.isWon === false && data.rowsPlayed >= 10) {
            window.EventBus.$emit('status:lost', data.rowsPlayed)
            
            this.$swal({
              icon: 'error',
              title: 'You lost...',
              text: 'You lost the game... Maybe you should hack your own backend to win?'
            })
          } 
        })
        .catch(() => {
          this.$swal({
            icon: 'error',
            title: 'Something went wrong',
            text: 'Check your developer console for more information'
          })
        })
    },

    getPinColour(colour) {
      return pins.getPinColour(colour)
    }
  },

  mounted() {
    window.EventBus.$on('field:update', this.checkStatus)
  }
}
</script>
