<template>
  <div class="board">
    <span v-if="!initialized">Add an API to get started with Mastermind!</span>
    <div class="game" v-if="initialized">
      <rows :id="id"></rows>
      <choice-maker :id="id"></choice-maker>
    </div>
  </div>
</template>

<script>
import mastermind from '../repositories/mastermind'
import Rows from './Rows'
import ChoiceMaker from './ChoiceMaker'

export default {
  name: 'Board',

  components: {
    Rows,
    ChoiceMaker
  },
  
  data() {
    return {
      'id': null
    }
  },

  computed: {
    initialized() {
      return !!(this.id)
    }
  },

  methods: {
    startGame() {
      mastermind
        .startNewGame()
        .then(id => {
          this.id = id;
        })
        .catch(() => {
          this.$swal({
              icon: 'error',
              title: 'Something went wrong',
              text: 'Check your developer console for more information'
          })
        })
    }
  },

  mounted() {
    window.EventBus.$on('url:set', this.startGame)
    window.EventBus.$on('url:clear', () => {
      this.id = null
    })
  }
}
</script>

<style>
.board {
  text-align: center;
}

span {
  color: #6b4d39;
  font-weight: bold;
}
</style>
