<template>
  <div class="connection">
    <h4>Connect Mastermind to your API</h4>
    <input type="text" placeholder="https://localhost:44367/api/Mastermind/" v-model="url">
    <br><br>
    <button @click="saveUrl" id="newGame" class="green">Save and start</button>
  </div>
</template>

<script>
import api from '../repositories/api'

export default {
  name: 'ApiConnector',

  data() {
    return {
      url: null
    }
  },

  methods: {
    saveUrl() {
      if(this.url) {
        api.setApiUrl(this.url)
        window.EventBus.$emit('url:set')
      }
    },

    clear() {
      this.url = '';

      api.setApiUrl('')
      window.EventBus.$emit('url:clear')
    }
  },

  mounted() {
    this.url = api.getApiUrl()
  }
}
</script>

<style>
.connection {
  text-align: center;
}

h4 {
  color: #6b4d39;
}

input[type=text] {
  border: none;
  padding: 8px;
  border-radius: 8px;
  width: 400px;
  color: #6b4d39;
  font-weight: bold;
}

input[type=text]::placeholder {
  color: gainsboro
}

</style>
